#header {
  position: relative;
  z-index: 99;
  border-bottom: 1px solid #e8eaf1;
  box-shadow: 0 1px 5px 0 $gray-200;

  .manage-link-container {
    width: 100%;
    .alert {
      text-align: center;
      width: 100%;
      padding: 4px;
      p {
        width: 100%;
      }
    }
  }

  .navbar {
    padding: rem-calc(13 25 15);
    align-items: center;

    @include media('≥tablet') {
      padding: rem-calc(16) rem-calc(37) rem-calc(15) rem-calc(39);
    }
  }

  .jcf-select {
    @include media('≥tablet') {
      width: rem-calc(160);
      height: rem-calc(34);
    }
  }

  .navbar-nav {
    padding-top: rem-calc(6);

    @include media('<tablet') {
      padding-top: rem-calc(15);
      padding-bottom: rem-calc(6);
    }

    >li {
      padding: 0 0 rem-calc(15);

      @include media('≥tablet') {
        padding: 0 rem-calc(19);
      }
    }

    .nav-link {
      padding: 0;
      color: $gray-600;

      &:hover {
        color: $green;
      }
    }
  }

  .login {
    margin-top: -2px;

    .dropdown-menu {
      left: auto;
      right: 0;
      margin-top: rem-calc(10);

      @include media('≥tablet') {
        right: rem-calc(-18);
      }

      @include media('≥tablet') {
        &:before,
        &:after {
          content: '';
          position: absolute;
          top: rem-calc(-7);
          right: rem-calc(22);
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 0 7px 7px 7px;
          border-color: transparent transparent $white transparent;
        }

        &:before {
          top: rem-calc(-8);
          right: rem-calc(21);
          border-width: 0 8px 8px 8px;
          border-color: transparent transparent $gray-200 transparent;
        }
      }
    }
  }

  .navbar-toggler {
    margin: 0 0 0 auto;
    outline: none;

    &:hover {
      border-color: $green;
      box-shadow: 0 0 5px 0 $gray-200;
    }
  }
}

.navbar-brand {
  padding-top: 0;
  padding: 0;
  margin-top: rem-calc(3);
  // -webkit-calc(width-of-sidebar - right-margin-of-image - width-of-the-logo + 30px-padding-for-#content)
  margin-right: -webkit-calc(12.0625rem - 70px);
  font-size: 0;
}

.navbar {
  background: $white;
  font-weight: $semibold;
  font-size: rem-calc(14);
}

.drop-nav {
  color: $gray-600;

  @include media('≥tablet') {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  @include media('<tablet') {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background: $white;
    box-shadow: 0 3px 5px -1px $gray-200;
    padding-left: 25px;
    padding-right: 25px;
  }

  .nav-link {
    padding: 0;
  }

  .login {
    .dropdown {
      position: relative;

      > a {
        position: relative;
        color: inherit;
        display: block;
        width: rem-calc(24);
        height: rem-calc(24);
        color: $gray-600;

        &:hover {
          text-decoration: none;
          color: $green;
        }
      }

      &:hover {
        &:after {
          position: absolute;
          top: 100%;
          right: 0;
          width: 100%;
          height:12px;
          content: '';
        }

        .dropdown-menu {
          display: block;
        }
      }
    }

    [class*='icon-'] {
      color: inherit;
      font-size: rem-calc(24);
      line-height: 1;
    }

    .dotted {
      position: absolute;
      bottom: 0;
      right: rem-calc(-4);
      width: rem-calc(12);
      height: rem-calc(12);
      border-radius: 50%;
    }
  }
}
