.pace {
  -webkit-pointer-events: none;
  pointer-events: none;

  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.pace-inactive {
  display: none;
}

.pace .pace-progress {
  background: $green;
  position: fixed;
  z-index: 1000;
  top: 94px;
  right: 100%;
  width: 100%;
  height: 3px;
}
