.card-wrap {
  border-top: 1px solid $gray-200;
  padding: rem-calc(20) rem-calc(15);
  font-size: rem-calc(10);

  @include media('≥widescreen') {
    padding: rem-calc(20) rem-calc(33);
  }

  h3 {
    font-size: rem-calc(14);
    margin: 0 0 rem-calc(4);
  }

  .title-card {
    margin-bottom: rem-calc(16);

    p {
      margin: 0;
    }
  }
}

.border-danger {
  border-color: rgba($danger, 0.5) !important;
}

.border-success {
  border-color: rgba($bluelight, 0.5) !important;
}

.card {
  border-radius: 2px;

  .bg-danger {
    background-color: rgba($danger, 0.1) !important;
  }

  .bg-success {
    background-color: rgba($bluelight, 0.1) !important;
  }
}

.card-body {
  min-height: rem-calc(73);
}

.card-header {
  color: $body-color;
  font-size: rem-calc(10);
  line-height: 1.2;
  border: 0;
  padding: rem-calc(10 16 8);

  &:first-child {
    border-radius: 0;
  }
}
