#sidebar {
  background: $white;
  width: 100%;
  padding: 0;
  display: block;

  @include media('≥tablet') {
    height: calc(100vh - 66px);
    height: calc(100vh - 95px);
    width: rem-calc(232);
    overflow: auto;
    border-right: 1px solid #e8eaf1;
  }

  .navbar-expand-md .navbar-collapse {
    display: block !important;
  }

  .navbar-toggler {
    margin: 10px rem-calc(37) 10px auto;

    @include media('<tablet') {
      display: block;
    }
  }

  .navbar-collapse {
    @include media('<tablet') {
      box-shadow: 0 3px 3px -1px $body-bg;
    }
  }
}

.sub-nav {
  width: 100%;
  font-size: rem-calc(15);
  line-height: 1.2;
  color: $body-color;
  font-weight: $medium;

  @include media('≥tablet') {
    padding-top: rem-calc(17);
  }

  >ul {
    >li {
      position: relative;

      &:after {
        position: absolute;
        top: 0;
        left: 0;
        width: 0;
        bottom: 0;
        content: '';
        background: $green;
        transition: width 0.5s;
      }

      &.active,
      &:hover {
        color: $green;
        background: $green-light;

        [class*="icon-"] {
          &:before {
            color: $green;
          }
        }


        &:after {
          width: rem-calc(2);
        }
      }

      @include media('<tablet') {
        width: 50%;


        &:nth-child(even) {
          &:after {
            left: auto;
            right: 0;
          }
        }
      }

      @include media('<phone') {
        width: 100%;

        &:nth-child(even) {
          &:after {
            left: 0;
            right: auto;
          }
        }
      }
    }

    @include media('<tablet') {
      flex-direction: row !important;
      flex-wrap: wrap;
    }
  }

  .nav-link {
    padding-top: rem-calc(10);
    padding-bottom: rem-calc(10);

    @include media('≥tablet') {
      padding-top: rem-calc(16);
      padding-bottom: rem-calc(16);
    }
  }

  a {
    color: inherit;
    padding-left: rem-calc(60);

    @include media('≥tablet') {
      padding-left: rem-calc(68);
    }

    [class*='icon-'] {
      position: absolute;
      left: rem-calc(27);
      top: 50%;
      transform: translateY(-50%);
      font-size: rem-calc(11);
      line-height: 1;

      @include media('≥tablet') {
        left: rem-calc(38);
      }
    }

    .icon-trunks {
      font-size: rem-calc(22);
      font-weight: $medium;
    }
    .icon-usage {
      font-size: rem-calc(19);
      font-weight: $medium;
    }
    .icon-endpoints {
      font-size: rem-calc(19);
      font-weight: $medium;
    }
    .icon-voice-applications {
      font-size: rem-calc(24);
      font-weight: $medium;
    }
    .icon-verified-caller-ids {
      font-size: rem-calc(19);
      font-weight: $medium;
    }
    .icon-profile {
      font-size: rem-calc(22);
      font-weight: $medium;
    }
    .icon-subaccounts {
      font-size: rem-calc(22);
      font-weight: $medium;
    }
    .icon-settings {
      font-size: rem-calc(19);
      font-weight: $medium;
    }
    .icon-porting {
      font-size: rem-calc(19);
      font-weight: $medium;
    }
    .icon-recharge{
      font-size: rem-calc(22);
      font-weight: $medium;
    }
    .icon-address {
      font-size: rem-calc(24);
      font-weight: $medium;
    }
    .icon-numbers {
      font-size: rem-calc(19);
      font-weight: $medium;
    }
    .icon-logs {
      font-size: rem-calc(19);
      font-weight: $medium;
    }
    .icon-history {
      font-size: rem-calc(19);
      font-weight: $medium;
    }
    .icon-verified {
      font-size: rem-calc(19);
      font-weight: $medium;
    }
    .icon-dashboard {
      font-size: rem-calc(19);
      font-weight: $medium;
    }
    .icon-byoc {
      font-size: rem-calc(24);
      font-weight: $medium;
    }
    .icon-analytics {
      font-size: rem-calc(19);
      font-weight: $medium;
    }
    .icon-sms-applications {
      font-size: rem-calc(24);
      font-weight: $medium;
    }
    .icon-clock {
      font-size: rem-calc(19);
      font-weight: $medium;
    }
  }
  .nav-item {
    &.sidebar-collapse-btn {
        transition: ease all 0.5s;
        background: $body-bg;
        position: absolute;
        width: 14.5rem;
        height:50px;
        bottom: 0px;
        .collapse-btn-icon {
            transition: ease all 0.5s;
            left: auto;
            right: 20px;
            &:before {
              font-size: 30px;
            }
        }
        &.collapsed-active {
          width: 59px;
        }
    }
  }
}
