.nav-tabs {
  padding: rem-calc(8) 0 0;
  font-size: rem-calc(14);
  line-height: 1.2;
  font-weight: $normal;
  min-height: 32px;

  .nav-item {
    padding: 0 rem-calc(20);

    &:first-child {
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;
    }
  }

  .nav-link {
    color: $gray-600;
    position: relative;
    padding: 0 0 rem-calc(6);
    border-bottom: 2px solid transparent;

    &.active {
      border-color: $green;
      font-weight: $semibold;
    }

    &:hover {
      color: $green;
    }
  }
}

.tab-wrap {
  display: block;
  align-items: stretch;
  margin-left: -5px;
  margin-right: -5px;
  width: 100%;

  @include media('≥tablet') {
    min-height: calc(100vh - 208px);
  }

  .jcf-scrollable-wrapper,
  .jcf-scrollable {
    width: 100% !important;
    // @include media('≥tablet') {
    //   display: flex;
    // }
  }

  .table-responsive {
    display: flex;
    // flex: 1 1 auto;
    padding-left: 5px;
    padding-right: 5px;
    overflow-x: auto;
    -ms-overflow-style:scrollbar;

    .holld-table-responsive {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding-right: 5px;
      padding-bottom: 0;
      min-width: 1030px;
    }
  }
}

.holld-table-responsive {
  @include media('<1100px') {
    width: 786px;
    min-width: 786px;
  }
}

.tab-content > .active {
  opacity: 1 !important;
}

.input-group {
  .input-group-prepend {
    width: 30%;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;

    .jcf-select {
      height: rem-calc(30);
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;

      &.jcf-drop-active {
        z-index: 3;
      }
    }

    .jcf-select-text {
      margin-left: rem-calc(13);
      margin-right: rem-calc(15);
      line-height: 28px;
    }

    .jcf-select-opener {
      width: rem-calc(15);
      font-size: 3px;

      &:after {
        right: rem-calc(8);
      }
    }
  }

  input[type='text'] {
    width: 48%;
    height: rem-calc(30);
    padding-left: rem-calc(8);
    padding-right: rem-calc(8);
    border-top-right-radius: $border-radius;
    border-bottom-right-radius: $border-radius;
  }
}

.panel-tab-title {
  padding: rem-calc(16 15 14);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include media('≥widescreen') {
    padding: rem-calc(16 19 14 33);
  }

  .panel-title {
    display: block;
    padding: rem-calc(4 10 0 0);
    font-size: rem-calc(16);
    color: $gray-600;
  }

  .box {
    @include media('≥tablet') {
      display: flex;
      align-items: center;

      .form-search {
        input[type=search] {
          min-width: 200px;
        }
      }
    }
  }

  .form_search {
    display: flex;
    align-items: center;
    margin-left: rem-calc(22);
  }

  .input-group {
    .jcf-select {
      width: rem-calc(114);
      height: rem-calc(30);
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      margin-right: -1px;

      &.jcf-drop-active {
        z-index: 3;
      }

      .jcf-select-text {
        margin-left: rem-calc(13);
        margin-right: rem-calc(15);
        line-height: 28px;
      }

      .jcf-select-opener {
        width: rem-calc(15);
        font-size: 3px;

        &:after {
          right: rem-calc(8);
        }
      }
    }

    input[type='search'] {
      width: rem-calc(156);
      height: rem-calc(30);
      padding-left: rem-calc(8);
      padding-right: rem-calc(8);
      border-top-right-radius: $border-radius-sm;
      border-bottom-right-radius: $border-radius-sm;
    }
  }

  .btn-back {
    display: inline-block;
    vertical-align: top;
    margin: rem-calc(7 0 5);
    font-size: rem-calc(12);
  }
}

.sub-title {
  padding-bottom: rem-calc(21);

  .panel-title {
    font-size: rem-calc(14);
    font-weight: $medium;
    padding: 0 rem-calc(10) 0 0;
  }
}

.table {
  font-size: rem-calc(12);
  line-height: 1.5;
  margin-bottom: 0;

  td {
    border-top: 0;
  }

  th,
  td {
    &:first-child {
      white-space: nowrap;

      @include media('≥widescreen') {
        padding-left: rem-calc(34);
      }
    }
  }

  b {
    font-weight: $semibold;
  }

  .list-link {
    @extend %listreset;
    margin-left: -15px;
    display: flex;
    flex-wrap: wrap;

    li {
      padding: 0 15px;
    }

    .collapsed {
      .c {
        display: none;
      }

      .e {
        display: block;
      }
    }

    .e {
      display: none;
    }
  }
}

.tab-content {
  // display: flex;
  flex: 1 1 auto;
  min-height: -webkit-calc(100vh - 242px);
  border-radius: $border-radius-sm;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  background: $white;
  box-shadow: 0 0 5px 0 $gray-300;
  padding-bottom: 30px;

  .tab-pane {
    width: 100%;
  }

  .filter {
    margin-bottom: rem-calc(13);
    font-size: rem-calc(10);
    font-weight: $semibold;
    display: flex;
    justify-content: flex-end;

    @include media('≥tablet') {
      margin-bottom: 0;
    }

    .list-inline-item:not(:last-child) {
      margin-right: rem-calc(30);
    }

    a {
      &:hover {
        text-decoration: none;
      }
    }
  }
}

.table {
  thead {
    th {
      text-transform: uppercase;
      background: #eff2f6;
      border-top: 0;
      border-bottom-width: 0;
      padding-top: rem-calc(9);
      padding-bottom: rem-calc(7);
      font-size: rem-calc(10);
      line-height: 1.2;
      font-weight: $medium;
      color: $body-color;
      vertical-align: middle;

      @include media('<widescreen') {
        &:first-child {
          padding-left: rem-calc(15);
        }
      }
    }
  }

  td {
    vertical-align: middle;

    @include media('<widescreen') {
      &:first-child {
        padding-left: rem-calc(15);
      }
    }
  }
}

.table-numbers {
  font-weight: $medium;

  tbody {
    td {
      &:first-child {
        padding-right: rem-calc(15);
        padding-left: rem-calc(15);

        @include media('≥widescreen') {
          width: rem-calc(63);
          padding-right: rem-calc(28);
          padding-left: 28px;
        }

        .custom-control {
          @include media('≥widescreen') {
            padding-left: 0;
          }
        }
      }
    }
  }
}

.width-157 {
  // width: 157px;
  white-space: nowrap;

  @include media('≥widescreen') {
    width: 157px;
  }
}

.coutry {
  display: flex;
  align-items: center;

  .flag {
    display: block;
    min-width: rem-calc(24);
    padding-right: rem-calc(7);
    margin-top: -1px;

    img {
      display: block;
    }
  }
}

.status {
  font-size: rem-calc(10);

  &:before {
    display: inline-block;
    content: '';
    width: rem-calc(7);
    height: rem-calc(7);
    margin-right: rem-calc(5);
    border-radius: 50%;
    background: $gray-400;
  }

  &.text-warning {
    &:before {
      background: $yellow;
    }
  }

  &.text-success {
    &:before {
      background: $blue;
    }
  }

  &.text-danger {
    &:before {
      background: $red;
    }
  }
}

.account-box {
  padding: rem-calc(151 0);

  .alert {
    display: inline-flex;
  }
}

.search-number {
  padding-bottom: 1px;
  border-top: 1px solid $gray-200;
  // border-bottom: 1px solid $gray-200;

  .body-box {
    .button-inline {
      margin-bottom: 0;
    }
  }
}

.footer-box {
  border-bottom: 1px solid $gray-500;
}

.form-row {
  margin-right: -($grid-gutter-width/2);
  margin-left: -($grid-gutter-width/2);

  > .col,
  > [class*='col-'] {
    padding-right: ($grid-gutter-width/2);
    padding-left: ($grid-gutter-width/2);
  }
}

.title-tab {
  padding: rem-calc(23 15 18);
  border-bottom: 1px solid $gray-200;

  @include media('≥widescreen') {
    padding: rem-calc(23 34 18);
  }

  h3 {
    margin: 0;
  }
}

.footer-tab {
  padding: rem-calc(23 5 18);
  border-top: 1px solid $gray-200;

  @include media('≥desktop') {
    padding: rem-calc(23 34 18);
  }
}

.body-box {
  padding: rem-calc(10 15);
  width: 100%;

  @include media('≥widescreen') {
    padding: rem-calc(10 34 7);
  }

  &.add {
    margin-top: rem-calc(4);
    border-top: 1px solid $gray-200;
    border-bottom: 1px solid $gray-200;
  }
}

.label,
label {
  margin-bottom: rem-calc(5);
  font-size: rem-calc(11);
  font-weight: $medium;
  color: $gray-600;
}

.footer-row {
  padding: rem-calc(14 18 13 32);
  font-size: rem-calc(12);
  font-weight: $semibold;
}

.form-add-number {
  padding-top: rem-calc(8);

  @include media('≥widescreen') {
    padding-left: rem-calc(19);
    padding-right: rem-calc(19);
  }

  .button-inline {
    padding-top: rem-calc(15);
  }
}

.body-wrap-line {
  border-top: 1px solid $gray-200;
  border-bottom: 1px solid $gray-200;
}

.line-bottom-none {
  border-bottom: 0;
}

.table-collapse {
  >td {
    padding-top: rem-calc(1);
    padding-bottom: rem-calc(17);
  }
}

.form_calendar {
  display: flex;
  align-items: center;
  margin-left: rem-calc(23);
  font-size: rem-calc(10);

  [type='search'] {
    width: rem-calc(162);
    margin-left: -1px;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
  }

  .separator {
    display: block;
    padding:7px 8px;
    margin: 0px -1px;
    border: 1px solid $gray-400;
  }
}

.input-daterange {
  display: flex;
  align-items: center;

  .form-control {
    border-radius: 0px !important;
    height: 30px;
    width: rem-calc(112);
    min-width: rem-calc(112);
  }
  .form-control:first-child() {
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }
}
