  #main {
  &.dashboard {
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    @media (min-width: 768px) {
      #content {
        width:100%;
        padding: rem-calc(26.5) rem-calc(34) 0 1.875rem;
      }
    }
    .tab-content {
      min-height: auto;
    }
    .tab-pane {
      border-top: 1px solid $gray-200;
      .container-fluid {
        padding: 0px rem-calc(34) rem-calc(26.5);
      }
    }
    .graph-container {
      margin-top: 31px;
    }
    .product-item, .use-case-item{
      display: flex;
      overflow: hidden;
      justify-content: space-between;

      .product-item-col {
        width: 31.5%;
        .dashboard-widget {
          position: relative;
          height: 100%;
          .info-container {
            &>div {
              width: 100%;
            }
            &>div:nth-child(2) {
              margin-bottom: 30px;
            }
          }
          .link-to {
            position: absolute;
            bottom: 15px;
          }
        }
      }
      .product-item-col:nth-child(1) {
        order: 0;
      }
      .product-item-col:nth-child(2) {
        order: 1;
      }
      .product-item-col:nth-child(3) {
        order: 2;
      }
    }
    .warning-container {
      display: flex;
      justify-content: center;
    }
    .use-case-item {
      margin-top: 0px;
    }
    .dashboard-widget {
      border: 1px solid $gray-200;
      border-radius: 2px;
      width: 100%;
      height: 100%;

      .auth-token-wrapper {
        [class*=icon-] {
          font-size: rem-calc(15);
          cursor: pointer;
          margin-top: 0px;
          padding: 0px 10px 0px 0px;
        }
        .delete-old-auth-link {
          padding-left: 10px;
          &:before {
            font-weight: $normal;
            color: $red;
          }
        }
      }

      #voice-container-legend, #sms-container-legend {
        margin-bottom: 30px;
        .legend-items {
          display: flex;
          align-items: center;
          margin-right: 10px;
          .legend-marker {
            display: block;
            width: 8px;
            height: 8px;
          }
          .legend-name {
            font-weight: $medium;
            font-size: rem-calc(8);
            margin-left: 5px;
          }
        }
      }
      .info-container {
        margin: 17px 25px;
      }
      [class^=col-] {
        padding: 0px;
        &>div {
          margin-top: 20px;
          margin-bottom: 0px;
          span {
            font-weight: $light;
          }
          span:first-child() {
            text-transform: uppercase;
            font-weight: $medium;
          }
          .help-text {
            font-size: 10px;
            margin-left: 35px;
            margin-top: -5px;
            font-weight: 100;
          }
        }
      }
      span {
        display: block;
        margin-bottom: 0.3125rem;
        font-size: rem-calc(12);
        font-weight: $normal;
      }
      .custom-checkbox {
        label {
          font-weight: $medium;
          font-size: rem-calc(12);
          padding-left: 5px;
          color: $body-color;
        }
      }
      .pricing-wrapper {
        display: flex;
        align-items: flex-end;
        line-height: 150%;
        position: relative;
        .credits {
          position: relative;
          font-size: 22px;
          top: 3px;
        }
        .add-credits {
          margin-bottom: 0px;
          padding-left: 10px;
        }
      }
    }
  }
  @media (max-width: 620px) {
    .product-item, .use-case-item{
      display: block !important;
      .product-item-col {
        width: 100% !important;
      }
    }
  }

}
