/* custom select styles */

.jcf-select {
  display: inline-block;
  vertical-align: top;
  position: relative;
  border: 1px solid $gray-400;
  border-radius: $border-radius;
  background: $white;
  margin: 0;
  width: 100%;
  height: rem-calc(28);
  cursor: pointer;

  &:hover {
    color: $primary;

    .jcf-select-text .jcf-option-hidden {
      color: $primary;
    }
  }
}

.jcf-select select {
  z-index: 1;
  left: 0;
  top: 0;
}

.jcf-select .jcf-select-text {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  display: block;
  font-size: rem-calc(12);
  line-height: 26px;
  margin: 0 rem-calc(25) 0 rem-calc(11);
  color: $gray-600;

  img {
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;
    margin-bottom: 2px;
  }
}

.jcf-select .jcf-select-opener {
  position: absolute;
  text-align: center;
  background: $white;
  width: rem-calc(26);
  bottom: 0;
  right: 0;
  top: 0;
  color: $gray-600;
  border-radius: $border-radius;
  font-size: 4px;
  line-height: 1;

  &:after {
    position: absolute;
    right: rem-calc(13);
    top: 50%;
    transform: translateY(-50%);
    content: '\e916';
    font-family: $font-ico;
  }
}

body>.jcf-select-drop {
  position: absolute;
  margin: 0;
  z-index: 9999;
}

body>.jcf-select-drop.jcf-drop-flipped {
  margin: 1px 0 0;
}

.jcf-select .jcf-select-drop {
  position: absolute;
  margin-top: 0px;
  z-index: 9999;
  top: 100%;
  left: -1px;
  right: -1px;
}

.jcf-select .jcf-drop-flipped {
  bottom: 100%;
  top: auto;
}

.jcf-select-drop .jcf-select-drop-content {
  border-radius: $border-radius;
  box-shadow: 0 1px 5px 0 $gray-200;
}

/* multiple select styles */

.jcf-list-box {
  overflow: hidden;
  display: inline-block;
  border: 1px solid $gray-400;
  min-width: 200px;
  margin: 0 15px;
}


/* select options styles */

.jcf-list {
  display: inline-block;
  vertical-align: top;
  position: relative;
  background: #fff;
  line-height: 14px;
  font-size: 12px;
  width: 100%;
  border-radius: $border-radius;
}

.jcf-list .jcf-list-content {
  vertical-align: top;
  display: inline-block;
  overflow: auto;
  width: 100%;
}

.jcf-list ul {
  list-style: none;
  padding: 6px 0;
  margin: 0;
}

.jcf-list ul li {
  overflow: hidden;
  display: block;
}

.jcf-list .jcf-overflow {
  overflow: auto;
}

.jcf-list .jcf-option {
  overflow: hidden;
  cursor: pointer;
  display: block;
  padding: 6px 13px;
  color: #656565;
  height: 1%;

  img {
    margin-right: 10px;
  }
}

.jcf-list .jcf-disabled {
  background: #fff !important;
  color: #aaa !important;
}

.jcf-select-drop .jcf-hover,
.jcf-list-box .jcf-selected {
  background: transparent;
  color: $green;
}

.jcf-list .jcf-optgroup-caption {
  white-space: nowrap;
  font-weight: bold;
  display: block;
  padding: 6px 13px;
  cursor: default;
  color: #000;
}

.jcf-list .jcf-optgroup .jcf-option {
  padding-left: 30px;
}

.jcf-drop-active {
  border-color: $green;
}


/* common custom form elements styles */

.jcf-disabled {
  background: #ddd !important;
}

.jcf-focus,
.jcf-focus * {
  border-color: $gray-400 !important;
}

.jcf-select-drop .jcf-option-hidden {
  display: none !important;
}

.jcf-hidden {
  display: none;
}


/* custom scrollbars styles */

.jcf-scrollable-wrapper {
  box-sizing: content-box;
  position: relative;
}

.jcf-scrollbar-vertical {
  position: absolute;
  cursor: default;
  background: transparent;
  width: 8px;
  bottom: 0;
  right: 0;
  top: 0;
}

.jcf-scrollbar-vertical .jcf-scrollbar-dec,
.jcf-scrollbar-vertical .jcf-scrollbar-inc {
  background: transparent;
  height: 8px;
  width: 8px;
  left: 0;
  top: 0;
  display: none !important;
}

.jcf-scrollbar-vertical .jcf-scrollbar-inc {
  top: auto;
  bottom: 0;
}

.jcf-scrollbar-vertical .jcf-scrollbar-handle {
  background: #bbb;
  height: 1px;
  width: 8px;
  border-radius: 3px;
}

.jcf-scrollbar-horizontal {
  position: absolute;
  background: transparent;
  right: auto;
  top: auto;
  left: 0;
  bottom: 14px;
  width: 1px;
  height: 8px;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-dec,
.jcf-scrollbar-horizontal .jcf-scrollbar-inc {
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
  background: transparent;
  height: 8px;
  width: 8px;
  display: none !important;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-inc {
  left: auto;
  right: 0;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-slider {
  display: inline-block;
  position: relative;
  height: 8px;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-handle {
  position: absolute;
  background: #bbb;
  height: 8px;
  border-radius: 3px;
}

.jcf-scrollbar.jcf-inactive .jcf-scrollbar-handle {
  visibility: hidden;
}

.jcf-scrollbar.jcf-inactive .jcf-scrollbar-dec,
.jcf-scrollbar.jcf-inactive .jcf-scrollbar-inc {
  background: transparent;
}


/* common custom form elements styles */

.jcf-disabled {
  background: #ddd !important;
}
