.chosen-container {
	width: 100% !important;
}
.application-collapse {
	.inline-definitions {
		display: table;
	}
}
.hidden {
	display: none
}
.invalid-feedback {
	display: block;
	padding-left: 0;
	.errorlist {
		padding-left: 0;
		list-style: none;
    margin-bottom: 0.5rem;
	}
}

.table-collapse.even {
	background: rgba($gray-200, 0.25);
}

dd.sip-truncate {
    width: 300px;
    overflow: hidden;
    word-wrap: break-word;
}

.table .list-link li:first-child {
	min-width: 80px;
}

.modal-dialog {
	.modal-content {
		.form-group {
			text-align: left;
		}
	}
}


#navbarSupportedContent {
    .jcf-select {
        .jcf-select-text {
            font-size:rem-calc(12);
            line-height: 280%;
        }
    }
}
.jcf-select-custom-select {
  .jcf-select-text {
    font-size: rem-calc(12);
  }
	.jcf-select-drop-content {
		.jcf-list {
			.jcf-list-content {
				.jcf-option {
					font-size: rem-calc(12);
				}
			}
		}
	}
}

.jcf-select-product-select-menu {
	border: none;
  background: #f5f7fa;
	.jcf-select-drop-content {
		.jcf-list {
			.jcf-list-content {
				.jcf-option {
					font-size: rem-calc(12);
					font-weight: $semibold;
					padding-bottom: 15px;
					&:first-child {
					    padding-top: 15px;
					}
				}

			}
		}
	}
	.jcf-select-opener {
		background: #f5f7fa;
	}
}

.chosen-container.chosen-container-single {
	.chosen-drop {
		.active-result {
			&.result-selected.highlighted {
				color:#2bb031;
			  background: transparent;
			}
			&:hover {
				color:#2bb031;
			  background: transparent;
			}
		}
	}
	.chosen-single {
		border-radius: 4px;
	  height: 30px;
	  border-color: rgb(206, 212, 218);
	  box-shadow: none;
	}
}


.fake-file-input-wrapper {
	display: flex;
	align-items: center;
	align-content: space-between;
	padding: 7px 13px;

	input {
		position: absolute;
		z-index: -1;
		display: block;
		overflow: hidden;
		width: 0px;
		height: 0px;
	}
	.uploaded-file {
		flex: 7;
		text-align: left;
		font-size: 10px;
		font-weight: $semibold;
	}
	.input-label {
		flex: 4;
		font-size: 10px;
    text-align: right;
		font-weight: $semibold;
	}
}

.auth-token-wrapper {
	width: 270px;
	display: flex;
	align-content: space-around;
	align-items: center;
	.show-auth-token {
		display: flex;
		align-self: center;
		margin: 4px 5px 0px 0px;
    font-size: 18px;
	}
}


.country-indicator {
	width: 40px;
	height: 28px;
	position: absolute;
	background: #eee;
	left: 30%;
	top: 1px;
	z-index: 1000;
	border-radius: 0px;
	font-weight: 300;
	display: flex;
	font-size: 12px;
	align-items: center;
	justify-content: center;
}


iframe#phlo-parent-frame {
    margin-left: -30px;
    margin-top: -35px;
    margin-right: -50px;
	height: 100vh;
}
.collapsed-sidebar {
	.icon {
		font-size: 28px !important;
		left: 10px !important;
	}
	.nav-item {
		padding-top: 15px;
	}
	.menu-text {
		transition: all 0.5s ease;
		display: none;
	}
	#content {
		width: calc(100% - 60px);
	}
	#sidebar {
		width: 60px !important;
	}
}
#sidebar {
	transition: all 0.5s ease;
	.menu-text {
		white-space: nowrap;
	}
	.icon {
		transition: all 0.5s ease;
	}
}

#content {
	transition: all 0.5s ease;

	.container-fluid{
		&.container-aligned-with-title {
			@include media('≥widescreen') {
				padding-left: rem-calc(33);
			}
		}
	}

	.debug-log-message {
		max-width: 200px;
		word-wrap: break-word;
	}

	.equal-width-btn {
		width: 100%;
		display: flex;
		justify-content: flex-end;

		.list-inline-item {
			width: 43%;
			.btn {
				width: 100%;
			}
		}
	}
}
