.card-block {
  flex: 1 1 auto;
  margin-top: rem-calc(20);
  background: $white;
  border-radius: $border-radius;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  box-shadow: 0 0 3px 0 $gray-500;

  .form-add-number {
    padding-top: rem-calc(20);
    padding-bottom: rem-calc(9);

    @include media('≥widescreen') {
      padding-right: rem-calc(38);
    }
  }

  .alert {
    margin-bottom: rem-calc(20);
  }
}

.question {
  p {
    margin-bottom: rem-calc(7);
  }
}

.button-wrap {
  padding-top: rem-calc(16);
  margin-bottom: rem-calc(8);
}

.card-table {
  tbody td {
    padding: rem-calc(12 5 13);
    vertical-align: middle;

    &:first-child {
      max-width: 85px;
      padding-right: 5px;
      padding-left: rem-calc(35);

      @include media('<widescreen') {
        padding-left: rem-calc(15);
      }
    }
  }

  .form-control {
    font-size: 12px;
    height: 25px;
    width: 40px;
    padding: 3px 5px;
    margin: 0;
  }
}

.form-echarge {
  .form-group {
    margin-bottom: rem-calc(14);
  }

  .checkbox-row {
    margin-bottom: rem-calc(9);
  }

  .custom-checkbox {
    label {
      font-size: rem-calc(12);
    }
  }

  .custom-control {
    padding-left: rem-calc(18);
  }

  .button-wrap {
    padding-top: rem-calc(10);
  }
}

.columns-separstor {
  position: relative;
  width: 100%;
  padding: 0 rem-calc(15);
  margin-top: rem-calc(3);

  .hold-columns {
    border-bottom: 1px solid $gray-200;

    @include media('≥tablet') {
      display: flex;
      justify-content: space-between;
    }

    .col-item {
      margin-bottom: rem-calc(14);
      padding-bottom: rem-calc(7);

      @include media('≥tablet') {
        border-left: 1px solid $gray-200;
        width: 64.5%;
        padding-left: rem-calc(45);
      }

      &:first-child {
        @include media('≥tablet') {
          width: rem-calc(220);
          padding-right: rem-calc(10);
          padding-left: 0;
          border: 0;
        }
      }
    }
  }

  .select-inline {
    margin-bottom: rem-calc(9);
  }
}

.balance {
  padding-top: rem-calc(5);
  margin-bottom: rem-calc(21);

  .label {
    display: block;
    margin-bottom: rem-calc(2);
  }

  .balance-money {
    display: block;
    font-weight: $medium;
    font-size: rem-calc(18);
  }
}

.box-inline {
  display: flex;
  align-items: center;
  font-size: rem-calc(10);

  >span {
    display: inline-block;
    margin-bottom: rem-calc(5);
  }

  .jcf-select {
    width: rem-calc(80);
    margin-left: rem-calc(8);
    margin-right: rem-calc(8);
    margin-bottom: rem-calc(5);
  }
}