// Color system
$green-light:                 #f4fbf4;

// stylelint-disable
$white:    #fff;
$gray-100: #f8f9fa;
$gray-200: #e8eaf1;
$gray-300: #dee2e6;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #626375;
$gray-700: #495057;
$gray-800: #343a40;
$gray-900: #212529;
$black:    #000;

$grays: ();
$grays: map-merge((
  "100": $gray-100,
  "200": $gray-200,
  "300": $gray-300,
  "400": $gray-400,
  "500": $gray-500,
  "600": $gray-600,
  "700": $gray-700,
  "800": $gray-800,
  "900": $gray-900
), $grays);

$blue:    #00a0db;
$bluelight:   #4a90e2;
$indigo:  #6610f2;
$purple:  #6f42c1;
$pink:    #e83e8c;
$red:     #e13b26;
$orange:  #fd7e14;
$yellow:  #ff9f00;
$green:   #2bb031;
$teal:    #20c997;
$cyan:    #17a2b8;

$colors: ();
$colors: map-merge((
  "blue":       $blue,
  "indigo":     $indigo,
  "purple":     $purple,
  "pink":       $pink,
  "red":        $red,
  "orange":     $orange,
  "yellow":     $yellow,
  "green":      $green,
  "teal":       $teal,
  "cyan":       $cyan,
  "white":      $white,
  "gray":       $gray-600,
  "gray-dark":  $gray-800
), $colors);

$primary:       $green;
$secondary:     $gray-600;
$success:       $blue;
$info:          $cyan;
$warning:       $yellow;
$danger:        $red;
$light:         $gray-100;
$dark:          #00163f;

$theme-colors: ();
$theme-colors: map-merge((
  "primary":    $primary,
  "secondary":  $secondary,
  "success":    $success,
  "info":       $info,
  "warning":    $warning,
  "danger":     $danger,
  "light":      $light,
  "dark":       $dark
), $theme-colors);

$min-width-container:         320px;
$max-width-container:         1200px;

$grid-gutter-width:           30px;

// wp-reset.scss - variables
$widget-margin:               20px;
$vertical-rhythm:             20px;
$wp-caption-border:           $border-width solid $border-color;

$font-family-base:            'soleil', $font-family-sans-serif;
// $font-family-base:            'AvenirNext', $font-family-sans-serif;
$font-ico:                    'icomoon';

//form-style.scss - variables
$input-font-family:           $font-family-base;
$input-font-size:             $font-size-base;

// $font-size-base:              0.75rem;
$font-size-base:              0.875rem;
$line-height-base:            1.42857;

$paragraph-margin-bottom:     1.125rem;

$h1-font-size:                1.375rem;
$h2-font-size:                1.25rem;
$h3-font-size:                1rem;
$h4-font-size:                0.75rem;

// font weight standard
$thin:                        100;
$light:                       300;
$normal:                      400;
$medium:                      400;
$semibold:                    600;
$bold:                        700;
$black-font:                  900;

// font weight soleil
// $thin:                        100;
// $light:                       300;
// $normal:                      400; // Regular
// $medium:                      600; // Book
// $semibold:                    700;
// $bold:                        700;
// $black-font:                  900;


// border: ;
$body-bg:                     #f5f7fa;
$body-color:                  #494a5b;

// Components
$border-radius:                     4px;
$border-radius-lg:                  6px;
$border-radius-sm:                  2px;

// custom-select
$custom-select-padding-y:           .375rem;
$custom-select-padding-x:           .75rem;
$custom-select-height:              $input-height;
$custom-select-indicator-padding:   1rem; // Extra padding to account for the presence of the background-image based indicator
$custom-select-line-height:         $input-btn-line-height;
$custom-select-color:               $input-color;
$custom-select-disabled-color:      $gray-600;
$custom-select-bg:                  $white;
$custom-select-disabled-bg:         $gray-200;
$custom-select-bg-size:             14px 8px; // In pixels because image dimensions
$custom-select-indicator:           url("../images/arrow-down.png");
$custom-select-border-width:        $input-btn-border-width;
$custom-select-border-color:        $input-border-color;
$custom-select-border-radius:       $border-radius-sm;

$custom-select-focus-border-color:  $input-focus-border-color;
$custom-select-focus-box-shadow:    0;

// Custom checkbox
$custom-control-indicator-size:         12px;
$custom-control-indicator-bg:           $white;

$custom-control-indicator-checked-color:        $white;
$custom-control-indicator-checked-bg:           theme-color("primary");
$custom-control-indicator-checked-box-shadow:   none;

$custom-control-indicator-focus-box-shadow:     none;

// Navs
$nav-tabs-border-color:             transparent;
$nav-tabs-border-width:             0;
$nav-tabs-border-radius:            0;
$nav-tabs-link-hover-border-color:  transparent;
$nav-tabs-link-active-color:        $green;
$nav-tabs-link-active-bg:           transparent;
$nav-tabs-link-active-border-color: $nav-tabs-link-hover-border-color;

// Tables
$table-cell-padding:                    16px 5px 13px;

$table-accent-bg:                       #f9fafb;

// Forms
$input-padding-y:                       $input-btn-padding-y !default;
$input-padding-x:                       $input-btn-padding-x !default;
$input-line-height:                     $input-btn-line-height !default;

$input-padding-y-sm:                    $input-btn-padding-y-sm !default;
$input-padding-x-sm:                    $input-btn-padding-x-sm !default;
$input-line-height-sm:                  $input-btn-line-height-sm !default;

$input-padding-y-lg:                    $input-btn-padding-y-lg !default;
$input-padding-x-lg:                    $input-btn-padding-x-lg !default;
$input-line-height-lg:                  $input-btn-line-height-lg !default;

$input-bg:                              $white;
$input-box-shadow:                      none;

$input-border-radius:                   $border-radius-sm;
$input-focus-box-shadow:                none;

$form-group-margin-bottom:              8px;

$form-feedback-invalid-color:           #f09c94;

// Modals
$modal-inner-padding:                   27px 26px;

$modal-content-bg:                      $white;
$modal-content-border-color:            $gray-600;
$modal-content-border-width:            0;

$modal-backdrop-bg:                     $body-color;
$modal-backdrop-opacity:                0.85;
$modal-header-padding:                  17px 26px;

$modal-md:                              500px;

// Buttons
//
// For each of Bootstrap's buttons, define text, background, and border color.

$btn-padding-y:               8px;
$btn-padding-x:               16px;

$btn-padding-y-sm:            6px;

$btn-disabled-opacity:        0.4;

$btn-font-weight:             600;
$btn-box-shadow:              none;
$btn-focus-width:             0;
$btn-focus-box-shadow:        none;
$btn-active-box-shadow:       none;

// Alerts
// Define alert colors, border radius, and padding.
$alert-padding-y:                   0.5rem;
$alert-padding-x:                   0.5rem;
$alert-margin-bottom:               1.5rem;
$alert-border-width:                0;

// Dropdowns
$dropdown-spacer:                   0;
$dropdown-border-color:             $gray-200;

$dropdown-link-color:               $body-color;
$dropdown-link-hover-color:         $primary;
$dropdown-link-hover-bg:            transparent;

$dropdown-link-active-color:        $primary;
$dropdown-link-active-bg:           transparent;

$dropdown-item-padding-y:           0.5rem;
$dropdown-item-padding-x:           0;
$dropdown-box-shadow:               0 0 3px $gray-200;
