.modal {
  font-size: rem-calc(12);

  .input-group-text {
    background: none;
    display: flex;
    align-items: center;
    font-size: rem-calc(10);
    height: rem-calc(28);
    line-height: 1;
    padding-left: rem-calc(8);
    padding-right: rem-calc(5);

    img {
      display: block;
    }

    .txt {
      padding-left: rem-calc(5);
    }
  }

  .input-group {
    margin-bottom: rem-calc(30);
  }

  p {
    margin-bottom: rem-calc(15);
  }

  .width-group-224 {
    width: rem-calc(224);
  }

  .number-box {
    text-align: center;
    margin-top: rem-calc(-5);
    margin-bottom: rem-calc(16);
    border-bottom: 1px solid $gray-200;
  }

  .flag {
    width: rem-calc(70);
    margin: 0 auto rem-calc(11);

    img {
      display: block;
      max-width: none;
      width: 100%;
    }
  }

  .tel {
    font-size: rem-calc(16);
    color: $body-color;
    font-weight: $semibold;
  }

  .city {
    display: block;
    margin-top: rem-calc(-3);
    margin-bottom: rem-calc(21);
  }

  .info-box {
    font-size: rem-calc(10);
    line-height: 1.6;
    color: $gray-600;

    .alert {
      margin-bottom: 0;
    }
  }

  textarea {
    height: rem-calc(60) !important;
  }

  .modal-body {
    &.add {
      padding-top: 10px;
      padding-bottom: 13px;
    }
  }
}

.list-info {
  @extend %listreset;
  display: inline-flex;
  flex-wrap: wrap;
  margin-bottom: rem-calc(5);

  >li {
    padding: 0 rem-calc(11) rem-calc(10);
  }
}

.item-info {
  font-size: rem-calc(12);
  color: $body-color;

  .title-info {
    display: block;
    margin-bottom: rem-calc(4);
    font-size: rem-calc(9);
    color: rgba($gray-600, 0.76);
    font-weight: $semibold;
  }

  .body-info {
    display: block;
    font-weight: $medium;
  }
}

.modal-footer {
  padding: rem-calc(12 15);
}

.modal-body {
  > p {
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.modal-successfully {
  padding-top: rem-calc(60);
  padding-bottom: rem-calc(33);
  font-size: rem-calc(10);
  line-height: 1.6;
  font-weight: $medium;

  h3 {
    margin-bottom: rem-calc(26);
  }

  .city {
    font-size: rem-calc(12);
    margin-bottom: rem-calc(29);
    line-height: 1.2;
  }

  >p {
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.modal-open .modal {
  min-height: 0;
  white-space: nowrap;
  text-align: center;
  padding-left: 0 !important;

  &:after {
    content: '';
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    width: 1px;
    overflow: hidden;
    margin: 0 0 0 -5px;
  }

  > div {
     display: inline-block;
     vertical-align: middle;
     white-space: normal;
  }
}

.modal-dialog {
  width: 100%;
  text-align: left;

  @include media('<tablet') {
    width: calc(100% - 30px);
  }
}

.modal-dialog-centered {

  .modal-content {

    .hold1 {
      width: 100% !important;
      background: $white;
      border-radius: 4px;
    }
  }
}
