.custom-control {
  min-height: rem-calc(12);
  line-height: 12px;
  padding-left: 17px;
}

.custom-control-label {
  cursor: pointer;

  &:before {
    top: 0;
    border-radius: 2px;
    border: 1px solid rgba($gray-600, 0.5);
  }

  &:after {
    top: 0;
  }
}

.custom-checkbox {
  .custom-control-label {
    font-weight: 400;
    &:before {
      border-radius: 2px;
    }
  }
}

.custom-checkbox .custom-control-input:checked ~ .custom-control-label {
  &:before {
    border: 0;
    border-color: $primary;
  }
}

.custom-checkbox.success-checkbox .custom-control-input:checked ~ .custom-control-label {
  &:before {
    border: 0;
    border-color: $success;
    background: $success;
  }
}

.custom-control-input:disabled ~ .custom-control-label {
  color: #d5d5d5;
}

.custom-control-input:disabled ~ .custom-control-label::before {
  background: none;
  border-color: #d5d5d5;
}
