.radio-buttons {
  padding: rem-calc(3 0 0);
  margin-bottom: rem-calc(11);

  .list-inline-item:not(:last-child) {
    margin-right: rem-calc(3);
    margin-bottom: rem-calc(5);
  }

  .custom-control-label {
    position: relative;
    padding: 3px 10px;
    text-align: center;
    min-width: rem-calc(55);
    border-radius: 100px;
    border: 1px solid $gray-500;

    &:before,
    &:after {
      display: none;
    }
  }

  .custom-control-input:checked ~ .custom-control-label {
    background: $white;
    border-color: $green;
    color: $green;
  }

  .custom-control {
    padding-left: 0;
  }
  
  .custom-radio {
    .custom-control-label{
      font-weight: 400;
    }
  }
}
