.input-group {
  .intl-tel-input.allow-dropdown.separate-dial-code {
    div:focus {
      box-shadow: none;
    }
    .flag-container{
      .selected-flag {
        font-size: 10px;
        width: 60px !important;
        background: white !important;
        border-right: 1px solid rgb(206, 212, 218);
        .iti-flag {
          transform: scale3d(0.8,0.8,0.8) !important;
        }
        .iti-arrow {
          display: none !important;
        }
        .selected-dial-code {
          padding-left: 22px;
        }
        &:focus {
          box-shadow: none;
          outline: none;
        }
      }
      .country-list {
        .flag-box {
          transform: scale3d(0.8,0.8,0.8) !important;
        }
      }
      &:focus {
        box-shadow: none;
      }
    }
    input, input[type=text], input[type=tel] {
      width: 225px;
      z-index: 0 ;
      padding-left: 70px !important;
    }
  }
}
