.alert {
  display: inline-flex;
  font-weight: $normal;
  font-size: rem-calc(10);
  line-height: 1.5;
  border-width: 1px;

  [class*='icon-'] {
    display: block;
    padding-top: rem-calc(1);
    margin-top: rem-calc(2);
    font-size: rem-calc(12);
    line-height: 1;
    margin-right: rem-calc(7);
  }

  p {
    margin: 0;
  }
}

.alert-warning {
  color: $body-color;
  background: #ffedb4;

  [class*='icon-'] {
    color: $yellow;
  }
}

.alert-primary {
  color: $body-color;
  border-color: $green;

  [class*='icon-'] {
    color: $primary;
  }
}

.alert-dismissible .close {
  padding-top: rem-calc(3);
}
